import {
  ADD_CITY_SUCCESS,
  MOVE_UP_CITY,
  ICitiesAction,
  MOVE_DOWN_CITY,
  REMOVE_CITY,
  RESTORE_CITY,
  SET_PENDING,
  ADD_CITY_ERROR,
} from './actions';

const initState: ICitiesState = {
  list: [],
  isPending: false,
  error: '',
};

export interface ICity {
  id: string;
  name: string;
  temperature: number;
  position: number;
  isDeleted: boolean;
  icon: string;
}

export interface ICitiesState {
  list: ICity[];
  isPending: boolean;
  error: string;
}

export const citiesSelector = (state: { cities: ICitiesState }) => {
  return state.cities;
};

const citiesReducer = (state: any = initState, action: ICitiesAction) => {
  switch (action.type) {

    case ADD_CITY_SUCCESS: {
      const { list } = state;
      const { city } = action.payload;
      city.position = list.length;
      const sameCity = list.filter((item: ICity) => item.id === city.id);
      let newList = [];
      // if we have the same, just update it
      if (sameCity.length) {
        newList = list.map((item: ICity) => {
          if (item.id === city.id) {
            item.name = city.name;
            item.temperature = city.temperature;
            item.icon = city.icon;
          }
          return item;
        });
      } else {
        newList = [...list, city];
      }
      return { ...state, error: '', list: newList };
    }

    case ADD_CITY_ERROR: {
      const { error } = action.payload;
      return { ...state, error };
    }

    case MOVE_UP_CITY: {
      const { id } = action.payload;
      const { list } = state;
      const newList = list.map((city: ICity) => {
        if (city.id === id) {
          city.position++;
        }
        return city;
      });
      return { ...state, list: [...newList] };
    }

    case MOVE_DOWN_CITY: {
      const { id } = action.payload;
      const { list } = state;
      const newList = list.map((city: ICity) => {
        if (city.id === id) {
          city.position--;
        }
        return city;
      });
      return { ...state, list: [...newList] };
    }

    case REMOVE_CITY: {
      const { id } = action.payload;
      const { list } = state;
      const newList = list.map((city: ICity) => {
        if (city.id === id) {
          city.isDeleted = true;
        }
        return city;
      });
      return { ...state, list: [...newList] };
    }

    case RESTORE_CITY: {
      const { id } = action.payload;
      const { list } = state;
      const newList = list.map((city: ICity) => {
        if (city.id === id) {
          city.isDeleted = false;
        }
        return city;
      });
      return { ...state, list: [...newList] };
    }

    case SET_PENDING: {
      const { switched } = action.payload;
      return { ...state, isPending: switched };
    }

    default:
      return state;
  }
};

export const cities = {
  cities: citiesReducer,
};
